body{font-family:"Roboto",sans-serif;font-size:24px;color:#292929}h1,h2,h3{font-size:30px;font-weight:700}p,span,div{font-size:18px}.paymentPage{display:flex;flex-direction:column;gap:40px;width:100%;max-width:100%;box-sizing:border-box}.sectionBlock{width:100%;box-sizing:border-box;background:#fff;border:1px solid #c4c4c4;box-shadow:0px 2px 2px rgba(0,0,0,.25)}.header{padding:30px;border-bottom:1px dashed #928f94}.title{margin:0;font-size:30px;font-weight:400;color:#292929}.formBody{padding:30px 30px 10px}.row3{display:flex;flex-wrap:wrap;gap:30px;margin-bottom:25px}.row1{margin-bottom:25px}.row1 .inputGroup{flex:1;width:auto;max-width:60%}.inputGroup{display:flex;flex:0 0 calc((100% - 60px)/3);flex-direction:column;width:calc((100% - 60px)/3);min-width:200px}.inputGroup label{margin-bottom:5px;font-size:16px;color:#928f94}.inputGroup input{width:100%;height:50px;padding:0 15px;font-size:24px;color:#292929;border:1px solid #928f94;border-radius:5px;outline:none;box-sizing:border-box}.inputGroup input:focus{border-color:#ffa800}.inputGroup input::placeholder{color:#928f94;opacity:.5}.paymentOptions{padding:30px}.optionRow{display:flex;flex-direction:column;align-items:flex-start;margin-bottom:20px}.checkboxLabel{display:flex;align-items:center;margin-bottom:15px;cursor:pointer}.checkboxLabel input{display:none}.checkboxLabel input:checked+.customCheckbox{border-color:#ffa800}.checkboxLabel input:checked+.customCheckbox::after{position:absolute;top:50%;left:50%;content:"";font-size:20px;color:#ffa800;transform:translate(-50%, -50%)}.checkboxLabel input:checked~.optionText{color:#ffa800}.customCheckbox{position:relative;width:28px;height:28px;margin-right:15px;background:#fff;border:2px solid #928f94;border-radius:5px;transition:.2s}.optionText{font-size:24px;font-weight:700;color:#928f94}.methodsList{display:flex;gap:60px;padding-left:45px}.methodItem{font-size:24px;font-weight:700;line-height:1.2;color:#292929}.divider{width:100%;height:1px;margin:20px 0 30px;background:rgba(0,0,0,0);border-top:1px dashed #928f94}.footerAction{display:flex;justify-content:flex-end;margin-bottom:50px}.buyBtn{padding:15px 60px;font-size:24px;font-weight:700;color:#fff;text-transform:uppercase;background:#ffa800;border:none;border-radius:5px;cursor:pointer;transition:.3s}.buyBtn:hover:not(:disabled){background:#e69500;box-shadow:0px 4px 4px rgba(0,0,0,.25)}.buyBtn:disabled{background:#928f94;cursor:default}